/* home */
.home {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}

.home-image {
  position: absolute;
  top: 50%;
  right: 10vw;        
  transform: translateY(-50%);
  width: 50vw;        
  max-width: none;
  opacity: 0.25; 
  pointer-events: none;
  user-select: none;
  z-index: 0;
  object-fit: cover;
  height: auto;
}

/* conteúdo de texto sobre a imagem */
.home-text {
  position: relative;
  z-index: 1;
  max-width: 600px;
}

/* título */
.home-title {
  text-shadow: 1px 1px 1px rgba(255, 25, 25, 0.2);
  transition: all 0.3s ease-in-out;
}

.home-title:hover {
  color: #42a7fa;
}

/* efeito de brilho */
.shine-text {
  animation: shine 3s linear infinite;
  color: #eee;
}

@keyframes shine {
  0% {
    opacity: 0.4;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.4;
  }
}

.icon-button-github {
  background-color: #35a0f780;
  border-color: #42a7fa;
}

.btn:hover {
  background-color: transparent !important;
  color: #42a7fa;
  border-color: #42a7fa;
}

