/* About */
.container-fluid p {
    text-align: justify;
    font-size: 18px;
    line-height: 35px;
}

/* skills */
.object-fit-cover {
  object-fit: cover;
}

/* certificados */
.accordion-button:focus {
  box-shadow: none;
}

.accordion-button:not(.collapsed) {
  background-color: #0d6efd;
  color: white;
}

.accordion-body {
  background-color: #1a1a1a;
  color: #f8f9fa;
  border-radius: 10px;
}

.btn-outline-primary {
  border-radius: 30px;
}


/* projects */
#projects {
  background-color: #0d0f14; 
  margin-top: 80px;
  margin-bottom: 80px;
}

.project-slide {
  width: 100%;
  max-width: 850px;
  height: 450px;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.6);
  transition: transform 0.4s ease;
}

.project-slide:hover {
  transform: scale(1.02);
}

.project-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease, filter 0.4s ease;
  filter: brightness(0.6);
}

.project-slide:hover .project-img {
  transform: scale(1.05);
  filter: brightness(0.8);
}

.overlay-dark {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* overlay escuro forte com transparência */
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
}

.project-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  text-align: center;
  padding: 1.5rem;
  color: #e5e5e5;
  width: 90%;
}

.title-blue {
  font-size: 1.8rem;
  font-weight: 800;
  color: #0d6efd; /* azul vibrante */
  margin-bottom: 0.7rem;
  text-shadow: 0 1px 3px rgba(0,0,0,0.7);
}

.desc {
  font-size: 1rem;
  color: #f8f9fa;
  background-color: rgba(13,110,253, 0.8);
  padding: 0.5rem;
  border-radius: 0.5rem;
  display: inline-block;
  max-width: 90%;
}

.buttons-wrapper .btn {
  font-size: 0.9rem;
  font-weight: 600;
  padding: 0.5rem 1rem;
  border-radius: 50px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.btn-outline-blue {
  color: #0d6efd;
  border: 2px solid #0d6efd;
}

.btn-outline-blue:hover {
  background-color: #0d6efd;
  color: #ffffff;
}

.btn-blue {
  background-color: #0d6efd;
  color: #ffffff;
  border: 2px solid #0d6efd;
}

.btn-blue:hover {
  background-color: #084298;
  border-color: #084298;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
  filter: invert(1) brightness(1.5);
}

.carousel-indicators button {
  background-color: #6c757d;
}

.carousel-indicators .active {
  background-color: #0d6efd;
}

  /* footer */ 
.footer {
  background-color: #000;
  font-size: 1.1rem;
}
  
.footer-icon {
  width: 180px;
  height: auto;
  margin-left: 35px;
}
  
.footer p {
  line-height: 30px;
}

@media (max-width: 575.98px) {
  .shine-text {
    width: 90%;
  }

  .img-fluid-certification {
    width: 100% !important;
  }

  .carousel-inner .desc {
    margin-left: 30px !important;
    width: 80% !important;
  }

  .carousel-inner .btn {
      margin-top: 5px;
  }

  .home-title {
    font-size: 35px;
  }
}
  
@media (max-width: 991.98px) {
  .navbar-collapse {
    padding: 0.5rem 1.5rem !important;
    text-align: left;
    line-height: 5px;
  }

  .navbar-nav .nav-link {
    padding-left: 0 !important;
    margin-left: 0 !important;
    text-align: left;
  }

  .navbar-nav {
    align-items: flex-start;
  }
}

@media (max-width: 768px) {
    .img-fluid {
        width: 50% !important;
        margin-left: 20px;
    }

    h2 {
        font-size: 1.7rem;
    }

    .accordion-button {
        font-size: 1rem !important;
    }

    .project-content p {
        margin-left: 100px !important;
    }
}